.content {
  display: flex; }
  .content .sidebar-wrap {
    width: 200px;
    float: left;
    padding: 20px 10px;
    margin-right: 10px;
    box-sizing: border-box;
    background: #fafafa; }
    .content .sidebar-wrap li {
      padding-left: 20px;
      box-sizing: border-box;
      line-height: 40px;
      color: #303133;
      font-size: 14px;
      transition: all .2s; }
      .content .sidebar-wrap li:hover {
        background: #ecf5ff; }
      .content .sidebar-wrap li a {
        display: block;
        width: 100%;
        height: 100%; }
  .content .content-right {
    flex: 1;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 0 0 8px rgba(232, 237, 250, 0.6), 0 2px 4px 0 rgba(232, 237, 250, 0.5); }
  .content .active-class {
    color: #409eff; }
